import Vue from 'vue'
import Router from 'vue-router'
import Auth from '@/api/auth'

const loadView = (name) => () => import(`@/views/${name}`)
const loadLayout = (name) => () => import(`@/components/layouts/${name}`)

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '',
      name: 'DefaultLayout',
      component: loadLayout('Default'),
      meta: {
        requiresAuth: true
      },
      beforeEnter (to, from, next) {
        if (from.matched.some(record => record.name === 'AuthLayout')) {
          next()
        } else {
          Auth.isAuth()
            .then(next)
            .catch(() => {
              Auth.logout()
              next({name: 'Login'})
            })
        }
      },
      children: [
        {
          path: '/',
          name: 'Home',
          component: loadView('Home')
        },
        {
          path: '/profile',
          name: 'Profile',
          component: loadView('Profile')
        }
      ]
    },
    {
      path: '',
      name: 'AuthLayout',
      component: loadLayout('Auth'),
      children: [
        {
          path: '/login',
          name: 'Login',
          component: loadView('Login')
        },
        {
          path: '/register',
          name: 'Register',
          component: loadView('Register')
        }
      ]
    },
    {
      path: '/not-found',
      name: 'Not Found',
      component: loadView('NotFound')
    },
    {
      path: '*',
      redirect: { name: 'Not Found' }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!Auth.checkToken()) {
      next({name: 'Login'})
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router
